<!-- App.svelte -->
<script>
  import { Router, Link, Route } from "svelte-routing";
  import Kalkulator from "./sider/kalkulator.svelte";
  import Startside from "./sider/startside.svelte";
  import Timeplan from "./sider/timeplan.svelte";
  import Spill from "./sider/spill.svelte";
  import Omoss from "./sider/om_oss.svelte";
  import Sidebar from './sidebar.svelte';

  let isOpen = true;
  let sideBarWidth = 0;
    let mainMarginLeft = 0;
    function openNav() {
      console.log("HERE");
      if (isOpen){
          sideBarWidth = 250;
          mainMarginLeft = 250;
      } else{
          sideBarWidth = 0;
          mainMarginLeft = 0;
      }
      isOpen=!isOpen;
    }

</script>

<Router url="/">
  <Sidebar sideBarWidth={sideBarWidth} mainMarginLeft={mainMarginLeft} openNav={openNav}>
    <Link to="/">Startside</Link>
    <Link to="timeplan">Timeplan</Link>
    <Link to="kalkulator">Kalkulator</Link>
    <Link to="spill">Spill</Link>
    <Link to="omoss">Om siden</Link>
  </Sidebar>
  <div id="main" style="margin-left: {mainMarginLeft}px;">
    <button class="openbtn"  on:click={openNav}>☰ Meny</button>
    <Route path="kalkulator" component="{Kalkulator}" />
    <Route path="timeplan" component="{Timeplan}" />
    <Route path="spill" component="{Spill}"/>
    <Route path="omoss" component="{Omoss}"/>
    <Route path="/" component="{Startside}"></Route>
  </div>
</Router>

<style>

.openbtn {
    font-size: 20px;
    cursor: pointer;
    background-color: #111;
    border: none;
    color: white;
    padding: 10px 15px;
    margin:0;
    transition: margin-left 0.5s;
  }
  
#main {
    transition: margin-left 0.5s;
    padding: 16px;
  }
    .link > :global(a) {
        color:white;
    }
    
  :global(a) {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 25px;
    color: #818181;
    display: block;
    transition: 0.3s;
    margin:0;
  }
</style>